/* -------------------------------------------------------------------------- *\
   $BASE
\* -------------------------------------------------------------------------- */
/* -------------------------------------------------------------------------- *\
   $BASE
\* -------------------------------------------------------------------------- */
/**
 * Apply a natural box layout model to all elements
 */
*,
:before,
:after {
  box-sizing: border-box;
}
/**
 * Remove text-shadow in selection highlight: h5bp.com/i
 * These selection rule sets have to be separate
 * Customize the background color to match your design
 */
::-moz-selection {
  background: #ed635e;
  color: #ffffff;
  text-shadow: none;
}
::selection {
  background: #ed635e;
  color: #ffffff;
  text-shadow: none;
}
/**
 * HTML
 */
html {
  background-color: #f5f5f5;
  color: #626262;
  font: 100%/1.5 "Source Sans Pro", sans-serif;
}
/**
 * Browse Happy
 */
.browsehappy {
  background-color: #f5f5f5;
  margin: 0;
  padding: 0.5em 0.75em;
  text-align: center;
}
.browsehappy a {
  border-bottom: 1px solid #9c9c9c;
  color: #626262;
  text-decoration: none;
  -webkit-transition: border-color 0.25s, color 0.25s;
  -moz-transition: border-color 0.25s, color 0.25s;
  -o-transition: border-color 0.25s, color 0.25s;
  transition: border-color 0.25s, color 0.25s;
}
.browsehappy a:focus,
.browsehappy a:hover {
  border-bottom-color: #ed635e;
  color: #ed635e;
}
/* -------------------------------------------------------------------------- *\
   $BLOCKQUOTE
\* -------------------------------------------------------------------------- */
/**
 * Indicates that the enclosed text is an extended quotation, as per:
 * http://html5doctor.com/cite-and-blockquote-reloaded
 *
 * E.g.:
 *
   <blockquote class="blockquote">
       <p></p>
       <cite>— <a href="#"></a></cite>
   </blockquote>
 *
 */
.blockquote {
  border-left: 3px #ed635e solid;
  margin: 1em 0 1em 1em;
  padding: 0 2em;
  /**
     * Paragraph
     */
  /**
     * Cite
     */
}
@media all and (min-width: 46.25em) {
  .blockquote {
    margin-top: 2em;
    margin-bottom: 2em;
  }
}
.blockquote p {
  quotes: "\201C" "\201D" "\2018" "\2019";
}
.blockquote p:before {
  content: open-quote;
}
.blockquote p:after {
  content: close-quote;
}
.blockquote cite {
  /**
         * Anchor
         */
}
@media all and (min-width: 46.25em) {
  .blockquote cite {
    font-size: 1.375em;
    /* 22 / 16 */
  }
}
.blockquote cite a {
  border-bottom: 1px solid #9c9c9c;
  color: #626262;
  text-decoration: none;
  -webkit-transition: border-color 0.25s, color 0.25s;
  -moz-transition: border-color 0.25s, color 0.25s;
  -o-transition: border-color 0.25s, color 0.25s;
  transition: border-color 0.25s, color 0.25s;
}
.blockquote cite a:focus,
.blockquote cite a:hover {
  border-bottom-color: #ed635e;
  color: #ed635e;
}
/**
 * Mixin
 */
/* -------------------------------------------------------------------------- *\
   $FIGURE
\* -------------------------------------------------------------------------- */
/**
 * The <figure> element is intended to be used in conjunction with the
 * <figcaption> element to mark up diagrams, illustrations, photos, as per:
 * http://html5doctor.com/the-figure-figcaption-elements
 *
 * E.g.:
 *
   <figure class="figure">
       <img src="" width="" height="" alt="" />
       <figcaption></figcaption>
   </figure>
 *
 */
.figure {
  margin: 1em 0;
  /**
     * Image
     */
  /**
     * Caption
     */
}
@media all and (min-width: 46.25em) {
  .figure {
    margin-top: 2em;
    margin-bottom: 2em;
  }
}
.figure img {
  height: auto;
  margin-bottom: 1em;
  max-width: 100%;
}
.figure figcaption {
  color: #9c9c9c;
}
@media all and (min-width: 46.25em) {
  .figure figcaption {
    font-size: 1.375em;
    /* 22 / 16 */
  }
}
/**
 * Mixin
 */
/**
 * FORMS
 */
/* -------------------------------------------------------------------------- *\
   TABLE OF CONTENTS
\* -------------------------------------------------------------------------- */
/**
 * FORM
 * FIELDSET
 * LABEL
 * INPUT
 * HELP TEXT
 * TOGGLE PASSWORD
 */
/* -------------------------------------------------------------------------- *\
   $FORM
\* -------------------------------------------------------------------------- */
.form {
  /**
     * Inline
     */
  /**
     * Well
     */
}
.form:before,
.form:after {
  content: " ";
  /* 1 */
  display: table;
  /* 2 */
}
.form:after {
  clear: both;
}
.form.form--inline .label.label--checkbox {
  margin-bottom: 0.5em;
}
.form.form--inline .input {
  margin-bottom: 0.75em;
}
.form.form--well {
  margin-bottom: 3em;
  position: relative;
}
.form.form--well .input {
  margin-top: 0;
  padding-right: 4.75em;
}
.form.form--well .input:focus,
.form.form--well .input:hover {
  background-color: #f5f5f5;
  border-color: #ffffff;
}
.form.form--well .btn {
  position: absolute;
  right: 0;
  top: 0;
}
@media all and (min-width: 46.25em) {
  .form.form--inline .label {
    clear: none;
    float: left;
    margin-right: 0.5em;
    width: auto;
  }
  .form.form--inline .label.label--checkbox {
    margin-bottom: 0;
    padding-top: 0.625em;
  }
  .form.form--inline .label.label--checkbox .input {
    float: none;
    margin-right: 0.25em;
  }
  .form.form--inline .input {
    float: left;
    margin-top: 0;
    margin-right: 0.5em;
    margin-bottom: 0;
    width: auto;
  }
  .form.form--inline .btn {
    float: left;
    margin-right: 0;
  }
}
@media all and (min-width: 46.25em) {
  .form.form--well {
    background-color: #ffffff;
    border-radius: 5px;
    padding: 1.5em;
  }
  .form.form--well + .alert {
    margin-top: -3em;
  }
  .form.form--well .input {
    font-size: 1.375em;
    /* 22 / 16 */
    height: 2.182em;
    /* 48 / 22 */
  }
  .form.form--well .input:focus,
  .form.form--well .input:hover {
    background-color: #f5f5f5;
    border-color: #f5f5f5;
  }
  .form.form--well .btn {
    right: 24px;
    top: 24px;
  }
}
@media all and (min-width: 61.25em) {
  .form.form--well .input {
    min-width: 20em;
    /* 320 / 16 */
  }
}
/* -------------------------------------------------------------------------- *\
   $FIELDSET
\* -------------------------------------------------------------------------- */
.fieldset {
  /**
     * Legend
     */
  /**
     * Bordered
     */
}
.fieldset .legend {
  color: #626262;
  font-size: 0.875em;
  /* 14 / 16 */
  font-weight: 700;
  margin-bottom: 1em;
  text-transform: uppercase;
}
.fieldset.fieldset--bordered {
  border: 3px solid #ffffff;
  margin-bottom: 1.5em;
  padding: 0.5em 1.25em 0 1.25em;
}
.fieldset.fieldset--bordered .legend {
  padding-right: 0.5em;
  padding-left: 0.5em;
}
/* -------------------------------------------------------------------------- *\
   $LABEL
\* -------------------------------------------------------------------------- */
.label {
  clear: both;
  color: #626262;
  display: block;
  font-weight: 700;
  margin-bottom: 1.5em;
  text-transform: uppercase;
  width: 100%;
  /**
     * Link
     */
  /**
     * Checkbox | Radio Button
     */
  /**
     * Disabled
     */
  /**
     * Inline
     */
  /**
     * Error
     */
  /**
     * Success
     */
  /**
     * Warning
     */
}
.label .label__link {
  border-bottom: 1px solid #9c9c9c;
  color: #626262;
  text-decoration: none;
  -webkit-transition: border-color 0.25s, color 0.25s;
  -moz-transition: border-color 0.25s, color 0.25s;
  -o-transition: border-color 0.25s, color 0.25s;
  transition: border-color 0.25s, color 0.25s;
  float: right;
  font-weight: 400;
  margin-top: 0.125em;
  text-transform: none;
}
.label .label__link:focus,
.label .label__link:hover {
  border-bottom-color: #ed635e;
  color: #ed635e;
}
.label.label--checkbox,
.label.label--radio {
  font-weight: 400;
  text-transform: inherit;
}
.label.label--disabled {
  color: #9c9c9c;
  cursor: not-allowed;
}
.label.label--inline {
  clear: none;
  float: left;
  margin-right: 1.5em;
  width: auto;
}
.label.has-error {
  color: #ff4136;
}
.label.has-error .input {
  border-color: #ff4136;
}
.label.has-error .help-text {
  color: #ff4136;
}
.label.has-success {
  color: #2ecc40;
}
.label.has-success .input {
  border-color: #2ecc40;
}
.label.has-success .help-text {
  color: #2ecc40;
}
.label.has-warning {
  color: #ff851b;
}
.label.has-warning .input {
  border-color: #ff851b;
}
.label.has-warning .help-text {
  color: #ff851b;
}
